.App {
  text-align: center;
}

.App-logo {
  position: relative;
  pointer-events: none;
}

.App-beta {
  position: absolute;
  bottom: 5px;
  right: 0;
  padding: 3px;
  border-radius: 2px;
  opacity: 0.9;
  font-size: 0.8em;
  top: 0;
  bottom: auto;
  color: #222;
  background: #ff9d00;
}

.App-header {
  background-color: #000;
  color: #a0541b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
}

.App-tagline {
  opacity: 0.7;
  background: #f77605;
  color: #000;
  padding: 10px;
  border-radius: 10px;
}

.App-header img {
  width: 100px;
  height: auto;
}

.App-link {
  color: #61dafb;
  opacity: 0.5;
}

.App-social {
  margin-top: 20px;
  padding-top: 20px;
  width: 350px;
}

.App-social a {
  display: inline-block;
  margin: 0 10px;
  color: #888;
}

.App-social a:hover {
  color: #eee;
}

.hjs-icon {
  color: inherit;
}
